.wrapper header.navbar {
    background: #000;
    opacity: .6;
}
.banner {
    height: 320px;
    margin-top: 0;
}

.wrapper header.navbar li a {
    color: #fff;
}
.wrap {
    padding: 80px 0 60px 0;
}

#wrap h3 {
    border-bottom: none;
    padding-bottom: 20px;
    padding-top: 0px;
}

#wrap .title {
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
}

#left {
    width: 720px;
    float: left;
    padding-top: 58px;
}

#right {
    width: 400px;
    float: right;
    padding-top: 58px;
}

.tit_title {
    border-bottom: 1px solid #ddd;
    padding-bottom: 25px;
    color: #999;
    font-size: 16px;
}

#left dl {
    text-align: center;
    padding-top: 25px;
}

#left dl dt {
    padding-bottom: 60px;
}

#left dl dd p {
    text-indent: 2em;
    padding-bottom: 30px;
    text-align: left
}

#right .list_th h4 {
    padding-bottom: 60px;
}

#right .list_th h4 img {
    vertical-align: middle
}

#right div dl dd p {
    font-size: 16px;
    line-height: 30px;
}